<template>
    <div>
        <h2 align="center">Q&A </h2>
        <div class="footer-form">
            <div id="mc_embed_signup">
                <input :key="key" type="text" placeholder="검색어 입력"
                       class="hide-on-focus" onfocus="this.placeholder = ''"
                       onblur="this.placeholder  =' 검색어 입력 '">
                <button type="submit" name="submit" id="newsletter-submit"
                        class="bt" @click="search">검색
                </button>
                <div class="mt-10 info"></div>
            </div>


            <b-container class="mt-4">
                <table align="center" width="70%" class="pinktable">
                    <thead>
                    <tr>
                        <td align="center">질문번호</td>
                        <td align="center">질문제목</td>
                        <td align="center">질문자아이디</td>
                        <td align="center">질문일시</td>
                        <td align="center">답변여부</td>
                    </tr>

                    <!-- eslint-disable vue/no-use-v-if-with-v-for,vue/no-confusing-v-for-v-if -->
                    <tr v-for="(qna, index) in qnas" :key="index" v-if="qna.qnaTitle.includes(key)">
                        <td align="center">{{qna.qnaNo}}</td>
                        <td align="center">
                            <router-link :to="'/detail/'+qna.qnaNo">{{qna.qnaTitle}}</router-link>
                        </td>
                        <td align="center">{{qna.qnaUserid}}</td>
                        <td align="center">{{qna.qnaDatetime}}</td>
                        <td align="center" v-if="qna.replyUserid == undefined">N</td>
                        <td align="center" v-else>Y</td>
                    </tr>
                    </thead>
                </table>

                <div class="mt-4" align="right" v-if="userid!=''">
                    <router-link to="/add">
                        <b-button variant="outline-danger">새 글 작성</b-button>
                    </router-link>
                </div>
                <div class="mt-4" align="right" v-if="userid==''">
                    <b-button variant="secondary" @click="alert">새 글 작성</b-button>
                </div>
            </b-container>
        </div>
    </div>
</template>

<script>
    import http from "@/util/http-common.js"

    export default {
        name: "list",
        props: {
            userid: String
        },
        data() {
            return {
                key: "",
                qnas: []
            }
        },
        methods: {
            search(event) {
                this.key = event.target.previousElementSibling.value;
            },
            alert() {
                alert("로그인 회원만 질문글을 작성하실 수 있습니다.");
            }
        },
        mounted() {
            http.get('/')
                .then((response) => {
                    this.qnas = response.data;
                })
                .catch((error) => {
                    console.dir(error);
                });
        }
    }
</script>

<style>
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }

    body {
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
        color: #333;
    }


    .footer-form input {
        width: 30%;
        height: 40px;
        padding: 10px 20px;
        border: 1px solid black;
        border-radius: 5px;
        margin: 15px 0 0 0;
    }

    .bt {
        position: absolute;
        margin: 15px 0 0 0;
        background: none;
        border: 0;
        cursor: pointer;
        padding: 13px 22px;
        background: #fb246a;
        line-height: 1;
        border-radius: 0 3px 3px 0;
        color: #fff;
    }


    .pinktable {
        text-align: left;
        line-height: 40px;
        border-collapse: separate;
        border-spacing: 0;
        border: 2px solid #ed1c40;
        width: 100%;
        border-radius: .25rem;
    }

    .pinktable thead tr:first-child {
        background: #ed1c40;
        color: #fff;
        border: none;
    }

    .pinktable th:first-child,
    .pinktable td:first-child {
        padding: 0 15px 0 20px;
    }

    .pinktable th {
        font-weight: 500;
    }

    .pinktable thead tr:last-child th {
        border-bottom: 3px solid #ddd;
    }

    .pinktable tbody tr:hover {
        background-color: #f2f2f2;
        cursor: default;
    }

    .pinktable tbody tr:last-child td {
        border: none;
    }

    .pinktable tbody td {
        border-bottom: 1px solid #ddd;
    }

    .pinktable td:last-child {
        padding-right: 10px;
    }

    .button {
        color: #aaa;
        cursor: pointer;
        vertical-align: middle;
        margin-top: -4px;
    }

    .edit:hover {
        color: #0a79df;
    }

    .delete:hover {
        color: #dc2a2a;
    }


    .pinktable2 {
        text-align: left;
        line-height: 40px;
        border-collapse: separate;
        border-spacing: 0;
        border: 2px solid #ed1c40;
        width: 40%;
        margin: 50px 50px 50px 50px;
        border-radius: .25rem;
    }

    .pinktable2 tr th:first-child {
        background: #ed1c40;
        color: #fff;
        border: none;
    }

    .pinktable2 th:first-child,
    .pinktable2 td:first-child {
        padding: 0 15px 0 20px;
    }

    .pinktable2 td {
        font-weight: 500;
        padding: 0 0 0 10px;
    }

    .pinktable2 th {
        font-weight: 500;
        padding: 0 15px 0 20px;
    }


    .pinktable2 thead tr:last-child th {
        border-bottom: 3px solid #ddd;
    }

    .pinktable2 tbody tr:hover {
        background-color: #f2f2f2;
        cursor: default;
    }

    .pinktable2 tbody tr:last-child td {
        border: none;
    }

    .pinktable2 tbody td {
        border-bottom: 1px solid #ddd;
    }

    .pinktable2 td:last-child {
        padding-right: 10px;
    }

    .head-btn1 {
        background: #fb246a;
        border: 1px solid #fb246a;
        color: #fff;
    }

    @media only screen and (min-width: 992px) and (max-width: 1199px) {
        .head-btn1 {
            padding: 5px 10px
        }
    }

    .head-btn2 {
        background: none;
        border: 1px solid #fb246a;
        color: #fb246a
    }

    @media only screen and (min-width: 992px) and (max-width: 1199px) {
        .head-btn2 {
            padding: 10px 10px
        }
    }
</style>