<script>
    import axios from '@/util/axios'

    export default {
        name: 'App',
        data() {
            return {
                userid: '',
            }
        },
        components: {},
        methods: {
            logout() {
                axios
                    .get('/logout')
                    .then(() => {
                        this.userid = '';
                    })
                    .catch((error) => {
                        console.dir(error);
                    });
            }
        },
        mounted() {
            axios
                .get('/logined')
                .then((response) => {
                    let name = response.data;
                    if (name != null) {
                        this.userid = name;
                    }
                    this.$router.push("/list");
                })
                .catch((error) => {
                    console.dir(error);
                });
            // 로그인이 되어있는지는 userid가 ''면 비로그인, userid가 ''이 아니면 로그인 상태로 판별
        }
    }
</script>

