<template>
    <div>
        <h3>질문글 작성</h3>
        <br><br>
        <b-container>
            <b-row>
                <b-col cols="2"/>
                <b-col cols="8">
                    <b-input-group prepend="ID">
                        <b-form-input
                                type="text"
                                class="form-control"
                                id="qna_userid"
                                ref="qna_userid"
                                v-model="qna_userid"
                                readonly="readonly"
                        />
                    </b-input-group>
                </b-col>
            </b-row>
            <b-row>
                <b-col cols="2"/>
                <b-col cols="8" class="mt-3">
                    <b-input-group prepend="제목 ">
                        <b-form-input
                                type="text"
                                class="form-control"
                                id="qna_title"
                                ref="qna_title"
                                placeholder="제목을 입력하세요"
                                v-model="qna_title"
                        />
                    </b-input-group>
                </b-col>
            </b-row>
            <b-row>
                <b-col cols="2"/>
                <b-col cols="8" class="mt-3">
                    <b-textarea
                            type="text"
                            class="form-control"
                            id="qna_contnet"
                            ref="qna_content"
                            placeholder="내용을 입력하세요"
                            v-model="qna_content"
                            rows="5"
                            max-rows="8"
                    ></b-textarea>
                </b-col>
            </b-row>
            <b-row>
                <b-col class="mt-3">
                    <b-button variant="danger" @click="checkHandler">등록</b-button>
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
    import http from '@/util/http-common';

    export default {
        name: 'board-Form',
        props: {
            type: {type: String},
            userid: String
        },
        data: function () {
            return {
                qna_no: '',
                qna_datetime: '',
                qna_userid: '',
                qna_title: '',
                qna_content: '',
            };
        },
        methods: {
            checkHandler() {
                let err = true;
                let msg = '';

                !this.qna_title &&
                ((msg = '제목 입력해주세요'), (err = false), this.$refs.qna_title.focus());
                err &&
                !this.qna_content &&
                ((msg = '내용 입력해주세요'),
                    (err = false),
                    this.$refs.qna_content.focus());

                if (!err) alert(msg);
                else {
                    this.createHandler();
                }

            },
            createHandler() {
                let qna = {};
                qna.qnaUserid = this.qna_userid;
                qna.qnaTitle = this.qna_title;
                qna.qnaContent = this.qna_content;
                http
                    .post('/', qna)
                    .then(({data}) => {
                        let msg = '등록 처리시 문제가 발생했습니다.';
                        if (data === 'success') {
                            msg = '등록이 완료되었습니다.';
                        }
                        alert(msg);
                    })
                    .catch((error) => {
                        console.dir(error);
                        alert('등록 처리시 에러가 발생했습니다.');
                    })
                    .finally(() => {
                        this.$router.push('/');
                    });
            },

        },
        mounted() {
            this.qna_userid = this.userid;
        }
    };
</script>

