<template>
    <div>
        <h3></h3>
        <b-container>
            <div id="question">
                <b-row>
                    <b-col cols="2"/>
                    <b-col cols="8">
                        <b-input-group prepend="질문 번호">
                            <b-form-input
                                    type="text"
                                    class="form-control"
                                    id="qna.qnaNo"
                                    ref="qna.qnaNo"
                                    :value="qna.qnaNo"
                                    readonly="readonly"
                            />
                        </b-input-group>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col cols="2"/>
                    <b-col cols="8" class="mt-1">
                        <b-input-group prepend="ID">
                            <b-form-input
                                    type="text"
                                    class="form-control"
                                    id="qna_userid"
                                    ref="qna_userid"
                                    :value="qna.qnaUserid"
                                    readonly="readonly"
                            />
                        </b-input-group>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col cols="2"/>
                    <b-col cols="8" class="mt-1">
                        <b-input-group prepend="작성시간">
                            <b-form-input
                                    type="text"
                                    class="form-control"
                                    id="qna.qnaDatetime"
                                    ref="qna.qnaDatetime"
                                    :value="qna.qnaDatetime"
                                    readonly="readonly"
                            />
                        </b-input-group>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col cols="2"/>
                    <b-col cols="8" class="mt-1">
                        <b-input-group prepend="제목 ">
                            <b-form-input
                                    type="text"
                                    class="form-control"
                                    id="qna_title"
                                    ref="qna_title"
                                    v-model="qna.qnaTitle"
                                    readonly="readonly"
                            />
                        </b-input-group>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col cols="2"/>
                    <b-col cols="8" class="mt-1">
                        <b-textarea
                                type="text"
                                class="form-control"
                                id="qna_contnet"
                                ref="qna_content"
                                v-model="qna.qnaContent"
                                rows="5"
                                max-rows="8"
                                readonly="readonly"
                        ></b-textarea>
                    </b-col>
                </b-row>
                <b-row v-if="qna.qnaUserid == userid">
                    <b-col cols="2"/>
                    <b-col cols="2" class="mt-1">
                        <router-link :to="'/update/'+qna.qnaNo">
                            <b-button variant="danger">수정</b-button>
                        </router-link>
                    </b-col>
                    <b-col cols="4"/>
                    <b-col cols="2" class="mt-1">
                        <b-button variant="outline-danger" @click="deleteQnA(qna.qnaNo)">삭제</b-button>
                    </b-col>
                </b-row>
            </div>
            <br><br>
            <div id="answer">
                <div v-if="qna.replyUserid !=undefined">
                    <b-row>
                        <b-col cols="2"/>
                        <b-col cols="8" class="mt-1">
                            <b-input-group prepend="ID">
                                <b-form-input
                                        type="text"
                                        class="form-control"
                                        id="qna_userid"
                                        ref="qna_userid"
                                        :value="qna.replyUserid"
                                        readonly="readonly"
                                />
                            </b-input-group>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col cols="2"/>
                        <b-col cols="8" class="mt-1">
                            <b-input-group prepend="작성시간">
                                <b-form-input
                                        type="text"
                                        class="form-control"
                                        id="qna_userid"
                                        ref="qna_userid"
                                        :value="qna.replyDatetime"
                                        readonly="readonly"
                                />
                            </b-input-group>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col cols="2"/>
                        <b-col cols="8" class="mt-1">
                            <b-textarea
                                    type="text"
                                    class="form-control"
                                    id="qna_contnet"
                                    ref="qna_content"
                                    :value="qna.replyContent"
                                    rows="5"
                                    max-rows="8"
                                    readonly="readonly"
                            ></b-textarea>
                        </b-col>
                    </b-row>
                </div>

                <div v-else-if="userid!=''">
                    <b-row>
                        <b-col cols="2"/>
                        <b-col cols="8">
                            <b-input-group prepend="ID">
                                <b-form-input
                                        type="text"
                                        class="form-control"
                                        id="qna_userid"
                                        ref="qna_userid"
                                        :value="replyUserid"
                                        readonly="readonly"
                                />
                            </b-input-group>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col cols="2"/>
                        <b-col cols="8" class="mt-1">
                            <b-textarea
                                    type="text"
                                    class="form-control"
                                    id="qna_contnet"
                                    ref="qna_content"
                                    v-model="replyContent"
                                    rows="5"
                                    max-rows="8"
                            ></b-textarea>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col>
                            <b-button variant="danger" class="mt-1" @click="replyWrite">작성</b-button>
                        </b-col>
                    </b-row>

                </div>
            </div>
        </b-container>
    </div>
</template>

<script>
    import http from '@/util/http-common.js'
    import moment from 'moment';

    export default {
        name: "detail",
        props: {
            userid: String
        },
        data() {
            return {
                qna: {},
                deleteNo: '',
                replyUserid: '',
                replyContent: '',
            }
        },
        methods: {
            replyWrite() {
                this.qna.replyUserid = this.replyUserid;
                this.qna.replyContent = this.replyContent;
                this.qna.replyDatetime = moment(new Date(), "YYYY-MM-DD HH:mm:ss").format();


                http.put('/', this.qna)
                    .then(() => {
                        alert("답변 작성 성공!");
                    })
                    .catch((error) => {
                        console.dir(error);
                        alert("답변 작성 중 통신 오류");
                    })
                    .finally(() => {
                        this.$router.push("/")
                    });
            },
            deleteQnA(num) {
                console.log(num);
                http.delete('/' + num)
                    .then((response) => {
                        if (response.data === 'success')
                            alert("삭제 성공!");
                        else
                            alert("데이터베이스 오류");
                    })
                    .catch((error) => {
                        alert("삭제 실패");
                        console.dir(error);
                    })
                    .finally(() => {
                        this.$router.push('/');
                    });
            }
        },
        mounted() {
            http.get('/' + this.$route.params.qnaNo)
                .then(({data}) => {
                    this.qna = data;
                    console.dir(data);
                })
                .catch(() => {
                    alert('통신 에러')
                });
            this.replyUserid = this.userid;
        }
    }
</script>

<style scoped>

</style>