<template>
    <div>
        <h3>질문글 수정</h3>
        <br><br>
        <b-container>
            <b-row>
                <b-col cols="2"/>
                <b-col cols="8">
                    <b-input-group prepend="ID">
                        <b-form-input
                                type="text"
                                class="form-control"
                                id="qna_userid"
                                ref="qna_userid"
                                :value="qna.qnaUserid"
                                readonly="readonly"
                        />
                    </b-input-group>
                </b-col>
            </b-row>
            <b-row>
                <b-col cols="2"/>
                <b-col cols="8" class="mt-3">
                    <b-input-group prepend="제목 ">
                        <b-form-input
                                type="text"
                                class="form-control"
                                id="qna_title"
                                ref="qna_title"
                                placeholder="제목을 입력하세요"
                                v-model="qna.qnaTitle"
                        />
                    </b-input-group>
                </b-col>
            </b-row>
            <b-row>
                <b-col cols="2"/>
                <b-col cols="8" class="mt-3">
                    <b-textarea
                            type="text"
                            class="form-control"
                            id="qna_contnet"
                            ref="qna_content"
                            placeholder="내용을 입력하세요"
                            v-model="qna.qnaContent"
                            rows="5"
                            max-rows="8"
                    ></b-textarea>
                </b-col>
            </b-row>
            <b-row>
                <b-col class="mt-3">
                    <b-button variant="danger" @click="checkHandler">수정</b-button>
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
    import http from '@/util/http-common';

    export default {
        name: 'board-Form',
        props: {
            type: {type: String},
            userid: String
        },
        data: function () {
            return {
                qna: []
            };
        },
        methods: {
            checkHandler() {
                let err = true;
                let msg = '';
                !this.qna.qnaTitle &&
                ((msg = '제목 입력해주세요'),
                    (err = false),
                    this.$refs.qna_title.focus());
                err &&
                !this.qna.qnaContent &&
                ((msg = '내용 입력해주세요'), (err = false), this.$refs.qna_content.focus());

                if (!err) alert(msg);
                else {
                    this.updateHandler();
                }
            },
            updateHandler() {
                http
                    .put('/', this.qna)
                    .then(({data}) => {
                        let msg = '수정 처리시 문제가 발생했습니다.';
                        if (data === 'success') {
                            msg = '수정이 완료되었습니다.';
                        }
                        alert(msg);
                        this.$router.push('/');
                    })
                    .catch(() => {
                        alert('수정 처리시 에러가 발생했습니다.');
                    });
            },
        },
        mounted() {
            http.get('/' + this.$route.params.qnaNo)
                .then(({data}) => {
                    this.qna = data;
                    console.dir(this.qna);
                })
                .catch(() => {
                    alert('통신 에러')
                });
            console.log(this.userid);
        }
    };
</script>

