import Vue from 'vue';
import VueRouter from "vue-router";

import list from '@/components/list'
import detail from "@/components/detail";
import Add from "@/components/Add";
import Update from "@/components/Update";


Vue.use(VueRouter);

export default new VueRouter({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'index',
            component: list
        },
        {
            path: '/list',
            name: 'list',
            component: list
        },
        {
            path: '/detail/:qnaNo',
            name: 'detail',
            component: detail
        },
        {
            path: '/add',
            name: 'Add',
            component: Add
        },
        {
            path: '/update/:qnaNo',
            name: 'Update',
            component: Update
        }
    ]
});